.advantages {
  background-color: $color-ghost-white;

  @include vp-767 {
    padding: 0;
  }
}

.advantages__wrapper {
  margin: 0 auto;
  padding: 107px 0 124px 0;
  max-width: 1040px;

  @include vp-1199 {
    padding: 92px 45px 94px;
  }

  @include vp-767 {
    padding: 61px 0;
  }
}

.advantages__title {
  letter-spacing: 1.5px;
  margin: 0 0 41px;
  text-align: center;

  @include vp-1199 {
    letter-spacing: 1.3px;
    margin-bottom: 35px;
  }

  @include vp-767 {
    letter-spacing: 1px;
    margin-bottom: 48px;
  }
}

.advantages__list {
  position: relative;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: 35% 23.5% 35%;
  gap: 145px 36px;
  list-style-type: none;

  @include vp-1199 {
    grid-template-columns: 1fr 1fr;
    gap: 40px 30px;
    margin: 0 auto;
    max-width: 930px;
  }

  @include vp-767 {
    grid-template-columns: 1fr;
    gap: 35px;
    justify-items: center;
    text-align: center;
  }
}

.advantages__item {
  position: relative;
  display: grid;
  grid-template-columns: auto 1fr;
  gap: 30px;
  margin: 0 0 0 20px;
  max-width: 335px;

  @media (min-width: 1200px) {
    grid-column: 1 / 2;
  }

  &::after {
    content: "";
    position: absolute;
    bottom: -78.5px;
    left: -10px;
    width: 100%;
    height: 2px;
    background-color: rgba(209, 10, 66, 0.2);
  }

  @media (min-width: 800px) and (max-width: 1199px) {
    justify-self: center;
  }

  @include vp-1199 {
    margin: 0;
    max-width: 320px;

    &::after {
      content: none;
    }
  }

  @include vp-767 {
    grid-template-columns: 1fr;
    gap: 0;
    padding: 0 34px;
    margin: 0;
    max-width: 100%;
    width: 100%;

    &::after {
      content: none;
    }
  }
}

.advantages__item:nth-child(2n+3),
.advantages__item:nth-child(2) {
  @media (min-width: 1200px) {
    grid-column: 3 / 3;
  }
}

.advantages__item:nth-last-child(-n+3) {
  &::after {
    content: none;
  }
}

.advantages__item--decorated {
  grid-column: 2/3;
  grid-row: 1/3;
  max-width: 240px;
  margin: 15px 0 5px -2px;
  padding: 50px 34px;
  background-color: $color-resolution-blue;
  color: $color-default-white;
  justify-self: center;
  text-align: center;

  @include vp-1199 {
    grid-column: 1/3;
    grid-row: 2/3;
    margin: 4px 0 -4px 0;
    padding: 30px 34px 38px 34px;
    width: 65%;
    max-width: 100%;
  }

  @include vp-767 {
    grid-column: 1/2;
    grid-row: 3/4;
    width: 100%;
    min-height: 239px;
    max-width: 100%;
    margin: 6px 0 10px;
    padding: 35px 34px 33px 34px;
  }
}

.advantages__item--decorated strong {
  margin: 0 0 0 -7px;
  font-weight: 700;
  font-size: 74px;
  line-height: 80px;

  @include vp-767 {
    margin: 0 0 0 -4px;
  }
}

.advantages__item--decorated span {
  display: block;
  margin: 0 0 31px 0;
  font-weight: 700;
  font-size: 26px;
  line-height: 39px;

  @include vp-1199 {
    margin: -4px 0 14px 0;
    font-size: 22px;
    line-height: 33px;
  }

  @include vp-767 {
    font-size: 22px;
    line-height: 33px;
    margin: -9px 0 8px 0;
  }
}

.advantages__item--decorated p {
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;

  @include vp-1199 {
    margin: 0 auto;
    max-width: 215px;
  }

  @include vp-767 {
    font-weight: 400;
    text-align: center;
    max-width: 215px;
    margin: 0 auto;
  }

  @media (min-width: 400px) and (max-width: 767px) {
    max-width: 100%;
  }
}

.advantages__item-title strong {
  font-size: 71px;
  line-height: 80px;
  font-weight: 700;
  margin: 0;
  min-width: 72px;
  background: linear-gradient(142.51deg, #d10a42 0%, #0041ca 75%);
  background-clip: text;
  color: transparent;

  @include vp-1199 {
    min-width: 58px;
    font-size: 58px;
    line-height: 70px;
  }

  @include vp-767 {
    font-size: 58px;
    line-height: 51px;
  }
}

.advantages__item-title small {
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  display: block;
  margin: -10px 0 0 0;
  color: $color-persian-blue;
  text-align: start;

  @include vp-1199 {
    text-align: start;
  }

  @include vp-767 {
    text-align: center;
    margin: -1px 0 0 0;
  }
}

.advantages__item-text {
  margin: 8px 0 0 0;
  text-align: start;

  @include vp-1199 {
    margin: 7px 0 0 0;
    text-align: start;
  }

  @include vp-767 {
    margin: 6px 0 0;
    text-align: center;
  }
}

.advantages__item-text span {
  margin: 0 0 5px 0;
  font-weight: 700;
  font-size: 22px;
  line-height: 33px;
  text-align: start;

  @include vp-1199 {
    font-size: 20px;
    line-height: 30px;
    margin: 0;
  }

  @include vp-767 {
    font-size: 20px;
    line-height: 30px;
    text-align: center;
    margin: 0;
  }
}

.advantages__item-text p {
  margin: 0;
  margin-top: 6px;
  font-weight: 300;
  font-size: 16px;
  line-height: 22.4px;
  text-align: start;

  @include vp-1199 {
    margin-top: 0;
  }

  @include vp-767 {
    margin: 0 auto;
    margin-top: 3px;
    max-width: 228px;
    text-align: center;
  }

  @media (min-width: 400px) and (max-width: 767px) {
    max-width: 100%;
    text-align: center;
  }
}
